@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

/*html{*/
/*-ms-touch-action: none;*/
/*winphone下默认触摸事件事件使用 e.preventDefault是无效的.目前解决方法是使用样式来禁用 */
/*}*/
body {
  font-family: Helvetica "Microsoft Yahei" sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*禁止ios和android用户选中文字*/
  /*-webkit-user-select: none;*/
  /*禁止ios长按时触发系统的菜单，禁止ios&android长按时下载图片*/
  /*-webkit-touch-callout: none;*/
  /*-ms-text-size-adjust: 100% !important;*/
  /*ios使用-webkit-text-size-adjust禁止调整字体大小*/
  /*-webkit-text-size-adjust: 100% !important;*/
}

a {
  text-decoration: none;
}

ol, ul {
  list-style: none;
}

a, button, input, textarea, select {
  -webkit-tap-highlight-color: transparent;
  -webkit-appearance: none;
  font-size: 100%;
}

.hide {
  display: none;
}

.abs-hide {
  display: none !important;
}

.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
  overflow: hidden;
}

.autoprefixer-test {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 300px;
  box-sizing: border-box;
  border: 1px solid red;
}

.autoprefixer-test div {
  flex-grow: 1;
  text-align: center;
  border: 1px solid red;
}

.icon1 {
  background-image: url("../img/sprite.png");
  background-position: -122px 0px;
  width: 37px;
  height: 53px;
  background-size: 159px 116px;
}

.icon2 {
  background-image: url("../img/sprite.png");
  background-position: 0px 0px;
  width: 56px;
  height: 53px;
  background-size: 159px 116px;
}

.icon3 {
  background-image: url("../img/sprite.png");
  background-position: -33px 0px;
  width: 23px;
  height: 26.5px;
  background-size: 79.5px 58px;
}

.icon4 {
  background-image: url("../img/sprite.png");
  background-position: 0px -31.5px;
  width: 16px;
  height: 26.5px;
  background-size: 79.5px 58px;
}

.float-test:before, .float-test:after {
  content: "";
  display: table;
}

.float-test:after {
  clear: both;
  overflow: hidden;
}

.float-test .l {
  float: left;
  width: 50%;
  border: 1px solid red;
  box-sizing: border-box;
}
